@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #065f46;
  color: #374151;
  cursor: url('assets/cursor.png') 0 22, auto;
}

.CursorTrail_wrapper{
  position:fixed;
  top:0;
  left:0;
  stroke:#ff273e!important;
  pointer-events:none;  
  z-index:998
}
@media(max-width:800px){
  .CursorTrail_wrapper{
      display:none
  }
  body {
    cursor: auto;
  }
}
.CursorTrail_cursor__IcwTS{
  width:100%;
  height:100%;
  position:absolute;
  top:0;
  left:0
}
.CursorTrail_line__X6w6g{
  stroke-dasharray:0;
  stroke-width:1;
  stroke-linecap:round;
  stroke:#ff273e!important
}
